@import url(https://fonts.googleapis.com/css2?family=Great+Vibes&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  scrollbar-width: none;
  -ms-overflow-style: none;

}

html {
  scroll-behavior: smooth;
}

::-webkit-scrollbar {
  display: none;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

@media (max-width: 768px) {
  .nav-links.mobile-menu-open {
    display: flex;
  }
}

.nav-links {
  z-index: 1000;
}
/* Importa a fonte local para manter a harmonia com a Navbar */
@font-face {
    font-family: 'MinhaFonte';
    src: url(/conviteCasamento/static/media/FontsFree-Net-lawrence-regular.8fcaf569.woff2) format('woff2');
    font-weight: 500;
    font-style: normal;
}

/* Overlay semi-transparente cobrindo a tela */
.modal-overlay {
    position: fixed;
    top: 0;
    left: 0;
    width: 100vw;
    height: 100vh;
    background-color: rgba(0, 0, 0, 0.5);
    display: flex;
    align-items: center;
    justify-content: center;
    z-index: 1000;
}

/* Container do modal */
.modal {
    background-color: #FFFCF8;
    padding: 2rem;
    border-radius: 8px;
    width: 90%;
    max-width: 500px;
    text-align: center;
    font-family: 'MinhaFonte', sans-serif;
}

/* Título do modal */
.modal h2 {
    font-size: 2.4rem;
    margin-bottom: 1.5rem;
}

/* Opções de presença (radio buttons) */
.attendance-options {
    margin: 1rem 0;
    font-size: 1.3rem;
}

.attendance-options label {
    margin: 0 1rem;
}

/* Seção para adicionar nomes */
.names-list {
    margin: 1rem 0;
    text-align: left;
    font-size: 1.3rem;
}

.names-list h3 {
    margin-bottom: 0.5rem;
    font-size: 1.6rem;
}

.add-name {
    display: flex;
    grid-gap: 0.5rem;
    gap: 0.5rem;
    margin-bottom: 1rem;
}

.add-name input {
    flex: 1 1;
    padding: 0.5rem;
    font-size: 1.2rem;
    border: 1px solid #ccc;
    border-radius: 4px;
}

/* Estilos para o input quando desabilitado */
.add-name input:disabled {
    background-color: #e0e0e0;
    /* Cinza claro */
    color: #888;
    cursor: not-allowed;
}

.add-name button {
    padding: 0.5rem 1rem;
    background-color: #3F6F90;
    color: #fff;
    border: none;
    border-radius: 4px;
    cursor: pointer;
    font-family: 'MinhaFonte', sans-serif;
    font-size: 1.2rem;
    transition: background-color 0.3s ease;
}

.add-name button:hover {
    background-color: #34536f;
}

/* Estilos para o botão "Adicionar" quando desabilitado */
.add-name button:disabled {
    background-color: #ccc;
    border-color: #ccc;
    cursor: not-allowed;
}

/* Lista de nomes */
.names-list ul {
    list-style: none;
    padding-left: 0;
}

.names-list li {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 0.5rem;
    border-bottom: 1px solid #ccc;
    padding: 0.2rem 0;
    font-size: 1.2rem;
}

.names-list li button {
    background-color: transparent;
    border: none;
    color: #d00;
    cursor: pointer;
    font-size: 1rem;
}

/* Ações do modal (botões de fechar e enviar) */
.modal-actions {
    display: flex;
    justify-content: space-between;
    margin-top: 1.5rem;
    grid-gap: 1rem;
    gap: 1rem;
}

.close-button,
.send-button {
    padding: 0.5rem 1rem;
    border: none;
    border-radius: 4px;
    font-size: 1.4rem;
    cursor: pointer;
    font-family: 'MinhaFonte', sans-serif;
    flex: 1 1;
}

.close-button {
    background-color: #ccc;
    color: #333;
}

.send-button {
    background-color: #3F6F90;
    color: #fff;
}

.close-button:hover {
    background-color: #bbb;
}

.send-button:hover {
    background-color: #34536f;
}

/* Estilos para o botão "Confirmar Presença" quando desabilitado */
.send-button:disabled {
    background-color: #ccc;
    border-color: #ccc;
    color: #888;
    cursor: not-allowed;
}

/* Responsividade para telas menores */
@media (max-width: 768px) {
    .modal {
        width: 95%;
        padding: 1.5rem;
    }

    .attendance-options {
        font-size: 1.1rem;
        margin: 0.8rem 0;
    }

    .attendance-options label {
        margin: 0 0.5rem;
    }

    .add-name input {
        font-size: 1rem;
        padding: 0.4rem;
    }

    .add-name button {
        font-size: 1rem;
        padding: 0.4rem 0.8rem;
    }

    .names-list h3 {
        font-size: 1.4rem;
    }

    .names-list li {
        font-size: 1rem;
    }

    .close-button,
    .send-button {
        font-size: 1.2rem;
        padding: 0.4rem 0.8rem;
    }
}

@media (max-width: 480px) {
    .modal {
        width: 95%;
        padding: 1rem;
    }

    .modal h2 {
        font-size: 2rem;
    }

    .attendance-options {
        font-size: 1rem;
    }

    .add-name input {
        font-size: 0.9rem;
    }

    .add-name button {
        font-size: 0.9rem;
    }

    .names-list h3 {
        font-size: 1.2rem;
    }

    .names-list li {
        font-size: 0.9rem;
    }

    .close-button,
    .send-button {
        font-size: 1rem;
    }
}
/* Evita overflow horizontal */
html,
body {
  overflow-x: hidden;
}

/* Reset básico para remover margens/paddings padrão */
* {
  padding: 0;
  box-sizing: border-box;
}

body {
  margin: 0;
  padding: 0;
}

/* Fonte local para o Navbar */
@font-face {
  font-family: 'MinhaFonte';
  src: url(/conviteCasamento/static/media/FontsFree-Net-lawrence-regular.8fcaf569.woff2) format('woff2');
  font-weight: 500;
  font-style: normal;
}

/* Barra de navegação principal */
.navbar {
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  /* Usa 100vw para garantir que não ultrapasse a viewport */
  z-index: 999;

  display: flex;
  align-items: center;
  justify-content: space-between;

  padding: 0.5rem 1rem;
  background-color: #fff;
  box-shadow: 0 20px 20px rgba(0, 0, 0, 0.1);
  font-family: 'MinhaFonte', sans-serif;
}

/* Logo */
.nav-logo {
  font-size: 2rem;
  /* Mantém o tamanho da logo */
  color: #333;
  white-space: nowrap;
}

/* Data (oculta por padrão) */
.nav-date {
  display: none;
}

/* Lista de links */
.nav-links {
  list-style: none;
  display: flex;
  align-items: center;
  grid-gap: 2rem;
  gap: 2rem;
}

/* Links padrão da Navbar */
.nav-links a {
  text-decoration: none;
  color: #333;
  font-weight: 600;
  font-size: 1.4rem;
  /* Aumentado de 1.2rem para 1.4rem para melhor legibilidade */
  transition: color 0.3s ease;
}

.nav-links a:hover {
  color: #666;
}

/* Botão de confirmação de presença (tamanho reduzido) */
.confirm-button {
  padding: 0.4rem 0.8rem;
  /* Padding reduzido */
  border: 2px solid #333;
  border-radius: 4px;
  background-color: #fff;
  color: #333;
  font-size: 1.2rem;
  /* Fonte reduzida de 1.4rem para 1.2rem */
  transition: background-color 0.3s ease, color 0.3s ease;
}

.confirm-button:hover {
  background-color: #333;
  color: #fff;
}

/* Ícone de menu hamburguer - escondido em telas grandes */
.mobile-menu-icon {
  display: none;
  flex-direction: column;
  grid-gap: 5px;
  gap: 5px;
  cursor: pointer;
}

.mobile-menu-icon .bar {
  width: 25px;
  height: 3px;
  background-color: #333;
}

/* Media Query para telas menores */
@media (max-width: 768px) {

  /* Reduz o padding da navbar para caber melhor na tela */
  .navbar {
    padding: 0.5rem;
    width: 100vw;
    /* Garante que a navbar não extrapola a largura da viewport */
  }

  /* Reduz o tamanho da logo */
  .nav-logo {
    font-size: 1.8rem;
  }

  /* Exibe o ícone do menu hamburguer */
  .mobile-menu-icon {
    display: flex;
  }

  /* Exibe a data centralizada vertical e horizontalmente */
  .nav-date {
    display: block;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    font-size: 1rem;
    color: #333;
  }

  /* Ajusta os links para ficarem menores e com menos espaçamento */
  .nav-links {
    position: fixed;
    top: 60px;
    /* Espaço suficiente para a navbar */
    left: 0;
    width: 100vw;
    /* Usa 100vw para evitar overflow horizontal */
    background-color: #fff;
    /* Mantém o fundo branco */
    flex-direction: column;
    align-items: center;
    grid-gap: 0.5rem;
    gap: 0.5rem;
    padding: 0.5rem 0;
    display: none;
    z-index: 998;
  }

  /* Exibe os links quando o menu estiver aberto e adiciona destaque com sombra */
  .nav-links.mobile-menu-open {
    display: flex;
    background-color: #fff;
    /* Fundo branco */
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.25);
    /* Sombra para destaque */
  }

  /* Mantém as cores originais para os links e para o botão,
     com tamanho um pouco menor para dispositivos móveis */
  .nav-links.mobile-menu-open a,
  .nav-links.mobile-menu-open .confirm-button {
    font-size: 1.1rem;
    /* Reduzido de 1.2rem para 1.1rem */
    color: #333 !important;
    /* Usa a cor original (#333) */
    border-color: #333;
  }

  /* Adiciona espaçamento superior ao conteúdo principal quando o menu está aberto */
  body.mobile-menu-open {
    padding-top: 220px;
    /* Ajuste dependendo da altura da navbar quando aberta */
  }
}

/* Overlay para escurecer o fundo quando o menu mobile estiver aberto */
body.mobile-menu-open::before {
  content: "";
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  background: rgba(0, 0, 0, 0.3);
  /* Ajuste a opacidade conforme desejado */
  z-index: 990;
  pointer-events: none;
}
.carousel {
    position: relative;
    /* Para telas menores (padrão): ocupa 80% da largura com até 800px */
    width: 80%;
    max-width: 800px;
    margin: 2rem auto;
    overflow: hidden;
}

/* Em telas maiores, reduz um pouco o tamanho do carrossel */
@media (min-width: 769px) {
    .carousel {
        /* Ajusta para 60% e max-width de 600px para que fique menor em relação à tela */
        width: 60%;
        max-width: 600px;
    }
}

/* Cada slide ocupa 100% da largura do contêiner */
.carousel-item {
    min-width: 100%;
    opacity: 0;
    transition: opacity 1s ease;
    position: absolute;
    top: 0;
    left: 0;
}

.carousel-item img {
    width: 100%;
    height: auto;
    display: block;
    border-radius: 4px;
}

/* Slide ativo */
.carousel-item.active {
    opacity: 1;
    position: relative;
}

/* Botões de navegação */
.carousel-button {
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    background-color: rgba(0, 0, 0, 0.5);
    /* Fundo escuro translúcido */
    border: none;
    color: #fff;
    padding: 0.5rem 1rem;
    cursor: pointer;
    font-size: 2rem;
    border-radius: 4px;
    z-index: 10;
}

.carousel-button.prev {
    left: 1rem;
}

.carousel-button.next {
    right: 1rem;
}

.carousel-button:hover {
    background-color: rgba(0, 0, 0, 0.7);
}

/* Responsividade para telas menores */
@media (max-width: 768px) {
    .carousel-button {
        font-size: 1.5rem;
        padding: 0.4rem 0.8rem;
    }
}
/* Importação da fonte "Lawrence" */
/* Declara a fonte "Lawrence" para uso no projeto, especificando o arquivo e formato */
@font-face {
  font-family: 'Lawrence';
  src: url(/conviteCasamento/static/media/FontsFree-Net-lawrence-regular.8fcaf569.woff2) format('woff2');
  font-weight: normal;
  font-style: normal;
}

/* Container do header */
/* Define o estilo geral do header */
.header {
  /* Define a cor de fundo como #FFFCF8 */
  background-color: #FFFCF8;

  /* Define a imagem de fundo */
  background-image: url(/conviteCasamento/static/media/background.6fc22055.jpg);
  background-repeat: no-repeat;
  /* Não repete a imagem */
  background-size: cover;
  /* Cobre todo o espaço, podendo cortar partes da imagem */
  background-position: top center;
  /* Posiciona a imagem no topo e centralizada horizontalmente */

  /* Define a altura mínima do header como 80% da viewport height (vh) */
  min-height: 80vh;

  /* Remove o padding superior, fazendo com que a imagem inicie bem no topo */
  padding-top: 0;

  /* Centraliza o conteúdo diretamente dentro do header */
  text-align: center;
  position: relative;
  /* Permite posicionamento relativo de elementos internos, se necessário */
}

/* Classe .hero */
/* Define o estilo da área "hero" dentro do header, onde geralmente ficam os textos principais */
.hero {
  /* Adiciona um espaço no topo de 250px para empurrar o conteúdo para baixo */
  padding-top: 250px;

  /* Define a fonte a ser utilizada para o texto */
  font-family: 'Lawrence', sans-serif;
  /* Define a cor do texto */
  color: #274D66;
  /* Limita a largura máxima do conteúdo a 800px */
  max-width: 800px;
  /* Centraliza horizontalmente o conteúdo com margem automática */
  margin: 0 auto;
  /* Centraliza o texto */
  text-align: center;
}

/* Título dentro da área hero (ex: Felipe & Esther) */
.hero h1 {
  font-size: 5.5rem;
  /* Define o tamanho da fonte para o título */
  margin: 0;
  /* Remove margens padrão */
  margin-bottom: 0.5rem;
  /* Adiciona uma pequena margem inferior */
  color: #344B68;
  /* Define a cor do título */
}

/* Subtítulo na área hero (ex: data, local, etc.) */
.hero p {
  margin: 0;
  /* Remove margens padrão */
  margin-top: 0.5rem;
  /* Adiciona uma margem superior para separar do título */
  font-size: 1.7rem;
  /* Define o tamanho da fonte para o subtítulo */
}

/* Botão de play/pause para a música de fundo */
.music-toggle-button {
  position: absolute;
  top: 1rem;
  right: 1rem;
  background-color: transparent;
  border: 2px solid #344B68;
  color: #344B68;
  font-family: 'Lawrence', sans-serif;
  font-size: 1rem;
  padding: 0.5rem 1rem;
  border-radius: 4px;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

.music-toggle-button:hover {
  background-color: #344B68;
  color: #fff;
}

/* ---------------- Responsive ---------------- */

/* Para telas com largura até 1024px */
@media (max-width: 1024px) {
  .hero {
    padding-top: 150px;
    /* Reduz o padding superior para telas menores */
    max-width: 90%;
    /* Aumenta a largura disponível para o conteúdo */
  }

  .hero h1 {
    font-size: 5rem;
    /* Reduz o tamanho do título */
  }

  .hero p {
    font-size: 1.6rem;
    /* Reduz um pouco o tamanho do subtítulo */
  }

  /* Ajusta o posicionamento da imagem de fundo para telas menores */
  .header {
    background-size: contain;
    /* Faz com que a imagem se ajuste dentro da tela */
    background-position: top center;
    /* Mantém a imagem posicionada no topo e centralizada */
  }
}

/* Para telas com largura até 768px */
@media (max-width: 768px) {
  .hero {
    padding-top: 120px;
    /* Ajusta o padding superior para começar mais próximo da navbar */
    max-width: 90%;
    /* Garante que o conteúdo ocupe a maioria da largura disponível */
  }

  .hero h1 {
    font-size: 4.5rem;
    /* Reduz o tamanho do título para telas menores */
  }

  .hero p {
    font-size: 1.4rem;
    /* Reduz o tamanho do subtítulo */
  }

  /* Ajusta o header para que comece abaixo da navbar */
  .header {
    padding-top: 0;
    /* Remove espaços desnecessários acima do header */
    background-size: contain;
    /* Ajusta o tamanho da imagem para que caiba na tela */
    background-position: top center;
    /* Garante que a imagem fique no topo e centralizada */
  }
}

/* Para telas com largura até 480px */
@media (max-width: 480px) {
  .hero {
    padding-top: 80px;
    /* Reduz ainda mais o espaço superior para dispositivos muito pequenos */
    max-width: 95%;
    /* Usa quase a totalidade da largura da tela */
  }

  .hero h1 {
    font-size: 3.5rem;
    /* Diminui o tamanho do título para melhor visualização em telas pequenas */
  }

  .hero p {
    font-size: 1.2rem;
    /* Reduz o tamanho do subtítulo */
  }

  /* Ajusta o header para remover espaço extra e garantir que a imagem de fundo se ajuste adequadamente */
  .header {
    padding-top: 0;
    /* Remove o padding superior */
    background-size: contain;
    /* Faz com que a imagem se ajuste dentro da tela */
    background-position: top center;
    /* Mantém a imagem no topo e centralizada */
  }
}

/* --- Ajustes para telas maiores (acima de 1024px) --- */
/* Reduz a sensação de zoom da imagem de background no header em telas maiores */
@media (min-width: 1025px) {
  .header {
    /* Alterado de "cover" para "contain" para exibir a imagem completa sem exagerar no zoom */
    background-size: contain;
    /* Reduz a altura do header para que o efeito de zoom seja menor */
    min-height: 70vh;
  }
}
/* Estilo para o componente de contagem regressiva */
.countdown {
  margin-top: 10rem;      /* Espaçamento superior opcional */
  color: #274D66;         /* Cor principal do texto */
}

/* Título da contagem */
.countdown-title {
  font-size: 1.5rem;
  margin-bottom: 1rem;
}

/* Container que agrupa cada item (dias, horas, minutos) */
.countdown-items {
  display: flex;
  justify-content: center; /* Centraliza horizontalmente */
  grid-gap: 3rem;
  gap: 3rem;              /* Espaço entre cada bloco */
}

/* Cada bloco individual (ex.: "40" + "dias") */
.countdown-item {
  display: flex;
  flex-direction: column; /* Número em cima, texto embaixo */
  align-items: center;
}

/* O número em destaque */
.number {
  font-size: 3rem;
  color: #274D66;
}

/* O rótulo (dias, horas, minutos) */
.label {
  font-size: 1.5rem;
  color: #555;
}

/* Media Query para telas menores */
@media (max-width: 768px) {
  .countdown {
    margin-top: 5rem;  /* Reduz o espaço superior em telas menores */
  }

  /* Ajusta o tamanho do título */
  .countdown-title {
    font-size: 1.2rem;
  }

  /* Mantém os itens em linha mesmo em telas menores */
  .countdown-items {
    grid-gap: 2rem;
    gap: 2rem; /* Reduz o espaço entre os itens */
    flex-wrap: nowrap; /* Garante que os itens fiquem lado a lado */
  }

  .countdown-item {
    flex-direction: column; /* Organiza os itens em coluna (número em cima, rótulo embaixo) */
  }

  /* Ajusta o tamanho das fontes */
  .number {
    font-size: 2.5rem;  /* Reduz o tamanho do número */
  }

  .label {
    font-size: 1.2rem;  /* Reduz o tamanho do rótulo */
  }
}

/* Media Query para telas extra pequenas */
@media (max-width: 480px) {
  .countdown {
    margin-top: 3rem;  /* Ajusta o espaço superior para telas muito pequenas */
  }

  /* Ajusta o tamanho do título */
  .countdown-title {
    font-size: 1rem;
  }

  /* Mantém os itens em linha para telas extra pequenas */
  .countdown-items {
    grid-gap: 1.5rem;
    gap: 1.5rem;               /* Reduz o espaço entre os itens */
    flex-wrap: nowrap;          /* Garante que os itens fiquem lado a lado */
  }

  .countdown-item {
    flex-direction: column; /* Mantém a disposição em coluna */
  }

  /* Ajuste de tamanho para os números */
  .number {
    font-size: 2rem;  /* Reduz ainda mais o tamanho do número */
  }

  /* Ajuste de tamanho para os rótulos */
  .label {
    font-size: 1rem;  /* Reduz ainda mais o tamanho do rótulo */
  }
}

